import { SxProps } from "@mui/system"
import { Box, IconButton, Paper, Typography } from "@rp/react/web/mui/core"
import { ArrowLeft, ArrowRight } from "@rp/react/web/mui/icons"

type Props = {
  limit: number
  page: number
  onPreviousPage?: () => void
  onNextPage?: () => void
  total?: number
  currentAmount?: number
  sx?: SxProps
}
const AdminTableNavigation = ({
  limit,
  page,
  total,
  onNextPage,
  onPreviousPage,
  sx,
  currentAmount = Infinity,
}: Props) => {
  const base = page * limit
  return (
    <Paper
      elevation={1}
      sx={{
        px: 2,
        py: 1,
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        backgroundColor: "elevation.dp01",
        ...sx,
      }}
    >
      <Box sx={{ display: "flex", gap: 3 }}>
        <Typography>Rows per page: {limit}</Typography>
        <Typography color="text.secondary">
          Showing {base + 1}-{base + Math.min(limit, currentAmount)}{" "}
          {total ? `out of ${total}` : ""}
        </Typography>
      </Box>
      <Box sx={{ display: "flex" }}>
        <IconButton disabled={onPreviousPage === undefined} onClick={onPreviousPage}>
          <ArrowLeft />
        </IconButton>
        <IconButton disabled={onNextPage === undefined} onClick={onNextPage}>
          <ArrowRight />
        </IconButton>
      </Box>
    </Paper>
  )
}
export { AdminTableNavigation }
